export default {
	name: 'ssNavbar',
	props: {
		navArr: {
			type: Array,
			default () {
				return [
					{
						title: '文本1',
						url: 'text1'
					}, {
						title: '文本2',
						url: 'text2'
					}, {
						title: '文本3',
						url: 'text3'
					}
				]
			}
		},
		tabCurrentIndex: {
			type: Number,
			default: 0
		}
	},
	computed: {
		itemWidth: function () {
			return (100 / this.navArr.length) + '%';
		}
	},
	data () {
		return {}
	},
	methods: {
		/**
		 * 导航栏navbar
		 * 点击事件
		 */
		tabChange (index) {
			this.$emit('navbarTap', index);
		}
	}
}
